                aboutTitle: "About Kalibari",
                content1: `In the electronic city in India, if you want to meet Kali Mata, then come to the famous Kali Temple in Bangalore. You will feel the satisfaction of life. You will feel that a Godly hand is always there to help you. In order to ensure the best wellbeing of you and your family members, you can arrange Puja at the famous electronic city Kali Bari. We are here to conduct the puja and thus to help you getting the best blessings from the Kali Mata. In your day to day life, if you are feeling dissatisfied, then don’t worry. God is there to help you always. Just make a call in a proper way and you will attain the strength to face the challenges in your life. Uncountable number of people comes here every day to attain the best blessings from their beloved Kali mata.`,
